body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  height: 50px;
  border-bottom: 1px solid white;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.title {
  font-size: 25px;
  padding: 10px;
}

.btn-restart {
  position: absolute;
  left: 12px;
  top: 58px;
}
.card {
  margin-top: 15px;
  margin-bottom: 15px;
  width: 200px;
  height: 250px;
  opacity: 1;
}

.hidden {
  opacity: 0.5;
}

.back {
  width: 200px;
  height: 250px;
  border: 3px solid red;
  border-radius: 10px;
  background-image: url('http://1.bp.blogspot.com/-wBzCym9NR88/VEJkekaMdjI/AAAAAAAAJeY/yk55XiTDDog/s1600/reves.jpg');
}

.card-content {
  width: 200px;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 3px solid black;
  border-radius: 10px;
}

